.vetskitchen-experts-advice {
  padding-top: 53px;
  margin-right: auto;
  margin-left: auto;
  max-width: 950px;
  text-align: center;
}
.vetskitchen-experts-advice__top {
  position: relative;
}
.vetskitchen-experts-advice__top .heading {
  color: #018077;
}
.vetskitchen-experts-advice__top .text {
  position: absolute;
  padding: 10px;
  top: 50%;
  right: 9%;
  max-width: 310px;
  color: #ffffff;
  background: rgba(114, 112, 112, 0.69);
  transform: translate(0, -50%);
  transition: 0.2s ease-in-out;
  opacity: 0;
  visibility: hidden;
}
.vetskitchen-experts-advice__top .text > *:last-child {
  margin-bottom: 0;
}
.vetskitchen-experts-advice__top:hover .text {
  opacity: 1;
  visibility: visible;
}
.vetskitchen-experts-advice__main {
  display: flex;
  justify-content: space-between;
  padding: 0 40px;
  margin-top: -54px;
  background-color: #eee;
}
.vetskitchen-experts-advice__main .left-content {
  align-self: center;
  padding-top: 60px;
  padding-bottom: 60px;
  max-width: 360px;
  font-size: 16px;
  color: #000000;
}
@media (min-width: 768px) and (max-width: 991px) {
  .vetskitchen-experts-advice__main .left-content {
    padding-right: 30px;
    max-width: 280px;
  }
}
.vetskitchen-experts-advice__main .right-content {
  align-self: flex-end;
}
@media (min-width: 768px) and (max-width: 991px) {
  .vetskitchen-experts-advice__main .right-content {
    margin-right: -30px;
  }
}
.vetskitchen-experts-advice__main .btn-orange {
  margin-top: 34px;
}
@media screen and (max-width: 767px) {
  .vetskitchen-experts-advice {
    padding-top: 40px;
  }
  .vetskitchen-experts-advice__main {
    flex-direction: column;
    margin-right: -12px;
    margin-left: -12px;
    background-image: url("../../assets/images/experts-advice-banner-mobile.webp");
  }
  .vetskitchen-experts-advice__main .left-content {
    padding-top: 80px;
    padding-bottom: 40px;
  }
  .vetskitchen-experts-advice__main .btn-orange {
    margin-top: 14px;
  }
  .vetskitchen-experts-advice__main .right-content {
    align-self: flex-start;
    margin-left: -40px;
  }
}